import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Card,
  CardContent,
  // CardHeader,
  // Checkbox,
  // FormControlLabel,
  FormHelperText,
  Grid,
  TextField,
  // Typography
} from '@material-ui/core';
// import FileDropzone from '../../../FileDropzone';
// import QuillEditor from '../../QuillEditor';
/* eslint-disable import/no-duplicates */
import firebase from '../../../../lib/firebase';
import { functions } from '../../../../lib/firebase';

const typeOptions = [
  {
    value: '',
    id: ''
  },
  {
    value: 'Administrador',
    id: 'administrator'
  },
  {
    id: 'workshop_agent',
    value: 'Encargado - Taller'
  },
  {
    id: 'dealership_agent',
    value: 'Vendedor'
  },
];

const VehicleCreateForm = (props) => {
  const navigate = useNavigate();
  const [branchs, setBranchs] = useState([]);

  useEffect(() => {
    firebase
      .firestore()
      .collection('dealership_branches')
      .get()
      .then((snapshot) => {
        const data = [{ id: '', name: '', vehicles_count: 0 }];
        snapshot.forEach((doc) => {
          /* eslint-disable object-shorthand */
          data.push({ ...doc.data(), id: doc.id });
        });
        console.log(data);
        setBranchs(data);
      });
  }, []);

  // const handleDrop = (newFiles) => {
  //   setFiles((prevFiles) => [...prevFiles, ...newFiles]);
  // };

  // const handleRemove = (file) => {
  //   setFiles((prevFiles) => prevFiles.filter((_file) => _file.path
  //     !== file.path));
  // };

  // const handleRemoveAll = () => {
  //   setFiles([]);
  // };

  const save = (values) => {
    // NOTE: Make API request
    console.log(values.email);
    // dispatch(update(values))
    const createClient = functions.httpsCallable('prueba');
    return createClient(values.email)
      .then((res) => {
        console.log(res);
        // values.key = res.data;
        const info = {
          avatar: 'https://firebasestorage.googleapis.com/v0/b/Mac-desarrollo.appspot.com/o/staff%2FYyCGDuzPuRSRj7JG1GXZawDODAq1%2F840_560%20(1).jpg?alt=media&token=aec0ca02-5912-4d32-b849-c7e2be8b8f55',
          branch: {
            id: values.branch_id,
            value: branchs.filter((brand) => brand.id === values.branch_id)[0].name,
          },
          email: values.email,
          name: values.name,
          phone: values.phone,
          rank: {
            id: values.rank,
            value: typeOptions.filter((brand) => brand.id === values.rank)[0].value,
          }
        };
        console.log(info);
        const admin = firebase
          .firestore()
          .collection('staff')
          .doc(res.data);
        admin
          .set(info)
          .then(() => {
            toast.success('Administrador Registrado', `Se ha registrado al administador ${values.name} exitosamente!`);
            navigate('/dashboard/taller/clientes');
            return true;
            // toast.success('Product created!');
          });
      })
      .catch((err) => {
        console.error(err);
        toast.error('Something went wrong!');
        return true;
      });
  };

  return (
    <Formik
      initialValues={{
        name: '',
        phone: 0,
        email: '',
        rank: '',
        branch_id: '',
        // submit: null
      }}
      validationSchema={Yup
        .object()
        .shape({
          name: Yup.string().max(255).required(),
          phone: Yup.number().min(0).required(),
          email: Yup.string().max(255).required(),
          rank: Yup.number().min(0).required(),
          branch_id: Yup.string().max(255).required(),
        })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          // NOTE: Make API request
          console.log(values.email);
          // dispatch(update(values))
          const createClient = functions.httpsCallable('prueba');
          return createClient(values.email)
            .then((res) => {
              console.log(res);
              // values.key = res.data;
              const info = {
                avatar: 'https://firebasestorage.googleapis.com/v0/b/Mac-desarrollo.appspot.com/o/staff%2FYyCGDuzPuRSRj7JG1GXZawDODAq1%2F840_560%20(1).jpg?alt=media&token=aec0ca02-5912-4d32-b849-c7e2be8b8f55',
                branch: {
                  id: values.branch_id,
                  value: branchs.filter((brand) => brand.id === values.branch_id)[0].name,
                },
                email: values.email,
                name: values.name,
                phone: values.phone,
                rank: {
                  id: values.rank,
                  value: typeOptions.filter((brand) => brand.id === values.rank)[0].value,
                }
              };
              console.log(info);
              const admin = firebase
                .firestore()
                .collection('staff')
                .doc(res.data);
              admin
                .set(info)
                .then(() => {
                  toast.success('Administrador Registrado', `Se ha registrado al administador ${values.name} exitosamente!`);
                  setStatus({ success: true });
                  setSubmitting(false);
                  navigate('/dashboard/taller/clientes');
                  return true;
                  // toast.success('Product created!');
                });
            })
            .catch((err) => {
              console.error(err);
              toast.error('Something went wrong!');
              setStatus({ success: false });
              setErrors({ submit: err.message });
              setSubmitting(false);
              return true;
            });
        } catch (err) {
          console.error(err);
          toast.error('Something went wrong!');
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
          return true;
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        // setFieldValue,
        touched,
        values
      }) => (
        <form
          onSubmit={handleSubmit}
          {...props}
        >
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Card>
                <CardContent>
                  <Grid
                    container
                    spacing={3}
                  >
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.name && errors.name)}
                        fullWidth
                        helperText={touched.name && errors.name}
                        label="Nombre y Apellido"
                        name="name"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.name}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.phone && errors.phone)}
                        fullWidth
                        helperText={touched.phone && errors.phone}
                        label="TelÃ©fono"
                        name="phone"
                        type="number"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.phone}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        fullWidth
                        label="Rango"
                        name="rank"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.rank}
                        variant="outlined"
                      >
                        {typeOptions.map((rank) => (
                          <option
                            key={rank.id}
                            value={rank.id}
                          >
                            {rank.value}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.email && errors.email)}
                        fullWidth
                        helperText={touched.email && errors.email}
                        label="Email"
                        name="email"
                        type="email"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.email}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={12}
                      xs={12}
                    >
                      <TextField
                        fullWidth
                        label="Sucursal"
                        name="branch_id"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.branch_id}
                        variant="outlined"
                      >
                        {branchs.map((branch) => (
                          <option
                            key={branch.id}
                            value={branch.id}
                          >
                            {branch.name}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
            <Grid
              item
              lg={2}
              md={2}
              xs={12}
            >
              {errors.submit && (
                <Box sx={{ mt: 3 }}>
                  <FormHelperText error>
                    {errors.submit}
                  </FormHelperText>
                </Box>
              )}
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  mt: 3
                }}
              >
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                  onClick={() => save(values)}
                >
                  Registrar Cliente
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      )}
    </Formik>
  );
};

export default VehicleCreateForm;
